<template>
  <div>
    <app-banner
      :image="page_idar_items[$route.params.lang]['banner']['image']"
      :text="page_idar_items[$route.params.lang]['banner']['text']">
      <div class="banner-buttons">
        <router-link class="button"
                     :to="'/'+$route.params.lang+'/idar/'+page_idar_items[$route.params.lang]['banner']['categories']['prev']['id']">
          {{page_idar_items[$route.params.lang]['banner']['categories']['prev']['name']}}
        </router-link>
        <router-link class="button" :to="'/'+$route.params.lang+'/idar'">Categories</router-link>
        <router-link class="button"
                     :to="'/'+$route.params.lang+'/idar/'+page_idar_items[$route.params.lang]['banner']['categories']['next']['id']">
          {{page_idar_items[$route.params.lang]['banner']['categories']['next']['name']}}
        </router-link>
      </div>
    </app-banner>
    <app-pagination></app-pagination>
    <app-info
      first="text"
      v-for="(item, index) in page_idar_items[$route.params.lang]['items']"
      :key="index">
      <!--<h4 slot="text" class="title">{{index}}</h4>-->
      <h4 slot="text" class="title">{{item['title']}}</h4>
      <p slot="text" class="description">{{item['description']}}</p>
      <router-link
        slot="text"
        class="button_arrow button_arrow__small white"
        :to="'/'+$route.params.lang+'/gallery/idar/'+index">
        <svg version="1.1" class="svg-line" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
             viewBox="0 0 150 5" style="enable-background:new 0 0 151 7;" xml:space="preserve">
             <polygon class="svg_path-line" points="151,3.5 147,0 147,3 0,3 0,4 147,4 147,7  "/>
        </svg>
      </router-link>


      <div slot="image">
        <app-block-image
          class="bg-image image"
          :bg_image="item['image']['url']"
        ></app-block-image>
      </div>
    </app-info>
  </div>
</template>

<script>
  import banner from './idar/banner.vue';
  import info from './global/info_divided.vue';
  import pagination from './global/pagination.vue';
  import block_image from './global/image_block.vue';

  export default {
    data() {
      return {
        page_idar_items: {
          ru: {
            banner: {
              image: "/static/images/kychine.jpg",
              text: "Дизайн интерьера и архитектура",
              categories: {
                next: {
                  id: 'category_3',
                  name: 'Дизайн фасада'
                },
                prev: {
                  id: 'category_1',
                  name: 'Придметный дизайн'
                }
              }
            },
            items: {
              project_1: {
                title: "Город Николаев",
                description: "Интерьер без излишествб который станет 'своим' и путешественникам и ленивцам-фрилансерам",
                image: {
                  url: "/static/img/Apartments-design_nikolaev.png",
                  alt: "дизайн интерьера николаев"
                }
              },
              project_2: {
                title: "Улица ахматовой",
                description: "Интерьер без излишествб который станет 'своим' и путешественникам и ленивцам-фрилансерам",
                image: {
                  url: "/static/img/Apartments-design_ahmatova.png",
                  alt: "lorem"
                }
              }
            }
          },
          en: {
            banner: {
              image: "/static/images/kychine.jpg",
              text: "Interiour design",
              categories: {
                next: {
                  id: 'category_3',
                  name: 'Design_1'
                },
                prev: {
                  id: 'category_1',
                  name: 'Design_3'
                }
              }
            },
            items: {
              project_1: {
                title: "Nikolayev",
                description: "Lorem ipsum domet ammet iclar besum",
                image: {
                  url: "/static/img/Apartments-design_nikolaev.png",
                  alt: "interiour design nikolayev"
                },
              },
              project_2: {
                title: "Ahmatova",
                description: "Lorem ipsum domet ammet iclar besum",
                image: {
                  url: "/static/img/Apartments-design_ahmatova.png",
                  alt: "interiour design project 2"
                }
              }
            }
          }
        }
      }
    },
    mounted(){
      $('.match-height').matchHeight({byRow: true});
    },
    components: {
      appBanner: banner,
      appBlockImage: block_image,
      appInfo: info,
      appPagination: pagination,
    }
  }
</script>

<style lang="sass" src="./../assets/style/banner-buttons.sass"></style>



